import React from 'react'

const CountBar = ({ classBase, items }) => (
    <div className={ `${classBase}__count-bar` }>
        { items.map(({ count, labelTop, labelBottom}) => (
            <span key={ count }>
                <span>{ count }</span>
                <span>
                    <div>{ labelTop }</div>
                    <div>{ labelBottom }</div>
                </span>
            </span>
        )) }
    </div>
)

export default CountBar
